/* eslint-disable no-unused-vars */

import React, { useState } from 'react'
import { useLocation } from 'react-router-dom'
import Filters from '../../../components/Sub/Filters/Filters'
import Diary from '../../../components/Main/DiaryPage/Diary/Diary'
import useStyles from './StudentsDiaryPage.style'
import api from '../../../api/api'
import useAsync from '../../../hooks/useAsync'
import { API_CURRENCY_DIRECTORY_ENDPOINT, API_VACANCIES_ENDPOINT } from '../../../utils/constants'
import useFilters from '../../../hooks/useFilters'
import ExportFeed from '../../../components/Sub/ExportDialogs/ExportFeed/ExportFeed'
import LoadingOverlay from '../../../components/Sub/DataTable/LoadingOverlay/LoadingOverlay'

const StudentsDiaryPage = ({ setSnack }) => {
  const [exportDialogOpen, setExportDialogOpen] = React.useState(false)
  const [cards, setCards] = useState([])
  const [filtersJSON] = useState({})
  const [isLoading, setIsLoading] = useState(false)
  const location = useLocation()
  const studentId = location.state?.studentId
  const studentName = location.state?.studentName
  const hook = useFilters({ id: studentId, name: studentName })
  const { run, data } = useAsync({
    data: [],
    error: null,
  })

  const openState = useState(false)
  const editOpenPopupState = useState(false)
  const cardDataState = useState(null)
  const [currencies, setCurrencies] = useState([])

  const getCurrencies = async () => {
    try {
      const response = await api.get(API_CURRENCY_DIRECTORY_ENDPOINT)
      setCurrencies(
        response.data.map((currency) => ({
          id: currency.id,
          name: currency.name,
          icon: currency.label,
        })),
      )
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  const getVacancies = async () => {
    let requestParams
    try {
      setIsLoading(true)
      if (hook.filters.id) requestParams = { profile_id: hook.filters.id }
      else requestParams = { profile_id: hook.filters.studentId }
      const response = await api.get(API_VACANCIES_ENDPOINT, {
        params: { ...requestParams },
      })
      setCards(Object.values(response?.data))
      return Promise.resolve(response?.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setIsLoading(false)
    }
  }

  React.useEffect(() => {
    run(getCurrencies())
  }, [])

  React.useEffect(() => {
    if (hook.filters?.studentId || hook.filters?.id) run(getVacancies())
  }, [hook.filters?.studentId, hook.filters?.id])

  const classes = useStyles()
  return (
    <main className={classes.root}>
      <ExportFeed openState={[exportDialogOpen, setExportDialogOpen]} directory={filtersJSON} />
      <Filters
        filterType="diary"
        hook={hook}
        filtersJSON={filtersJSON}
        handleDownload={() => setExportDialogOpen(true)}
      />
      {isLoading ? (
        <LoadingOverlay />
      ) : (
        <Diary
          cards={cards}
          states={{ openState, editOpenPopupState, cardDataState }}
          handlers={{}}
          currencies={currencies}
          readOnly
        />
      )}
    </main>
  )
}

export default React.memo(StudentsDiaryPage)
