/* eslint-disable no-unused-vars */
/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import useStyles from './Filters.style'
import SearchBar from '../Searchers/SearchBar/SearchBar'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'

function ApplicationFilters({
  searchState,
  searchName,
  dateProps,
  errors,
  handleDownload,
  control,
  courseProps,
  cohortsProps,
  typesProps,
  statusProps,
  staffProps,
}) {
  const classes = useStyles()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { courseId, courses, handleChangeCourse } = courseProps
  const { cohortId, cohorts, handleChangeCohort, isCohortDisabled } = cohortsProps
  const { typeId, types, handleChangeType } = typesProps
  const { statusId, statuses, handleChangeStatus } = statusProps
  const { staffId, staff, handleChangeResponsible } = staffProps
  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
              Заявки
            </Typography>
            <SearchBar valueState={searchState} nameValue={searchName} />
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
          </div>
          <div className={classes.buttonContainer}>
            <Button
              className={`${classes.button} ${classes.button_type_small} ${classes.button_color_white}`}
              onClick={handleDownload}
            >
              Выгрузить данные
            </Button>
          </div>
        </div>

        <div className={classes.filters__container}>
          <div className={classes.filters}>
            <TextfieldSelect
              name="course_filter"
              control={control}
              value={courseId}
              onChange={handleChangeCourse}
              items={courses}
              width={288}
            />
            {!isCohortDisabled && (
              <TextfieldSelect
                name="cohorts_filter"
                control={control}
                value={cohortId}
                onChange={handleChangeCohort}
                items={cohorts}
                disabled={isCohortDisabled}
                width={130}
              />
            )}

            <TextfieldSelect
              name="type_filter"
              control={control}
              value={typeId}
              onChange={handleChangeType}
              items={types}
              width={230}
            />

            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={145}
            />

            <TextfieldSelect
              name="responsible_filter"
              control={control}
              value={staffId}
              onChange={handleChangeResponsible}
              items={staff}
              width={230}
            />
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(ApplicationFilters)
