/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import useStyles from './Filters.style'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'
import TextfieldSearch from './textfield-search/textfield-search'
import { ControlledSelectField } from '../FormElements'

function VacanciesFilters({
  searchString,
  handleSearchChange,
  dateProps,
  errors,
  control,
  multipleCoursesProps,
  statusProps,
  handleAddVacancy,
}) {
  const classes = useStyles()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { coursesOptions, selectedCourses, handleChangeMultipleCourses } = multipleCoursesProps
  const { statusId, statuses, handleChangeStatus } = statusProps
  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
              Управление вакансиями
            </Typography>
            <TextfieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder="Поиск компании"
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
            />
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
          </div>
        </div>

        <div className={classes.filters__container} style={{ alignItems: 'flex-start' }}>
          <div className={classes.filters} style={{ alignItems: 'flex-start' }}>
            <ControlledSelectField
              tipContent="Все курсы"
              tipBlack
              value={selectedCourses || []}
              onChange={handleChangeMultipleCourses}
              items={coursesOptions || []}
              displayEmpty
              multiple
              maxWidth={288}
            />
            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={167}
            />
          </div>
          <div className={classes.filters__buttonsContainer}>
            <Button className={`${classes.button} ${classes.button_type_long}`} onClick={handleAddVacancy}>
              Добавить вакансию
            </Button>
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(VacanciesFilters)
