/* eslint-disable no-nested-ternary */
import React from 'react'
import { format, differenceInDays } from 'date-fns'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { filtersSchema } from '../../../utils/schemas'
import ApplicationFilters from './ApplicationFilters'
import StudentsFilters from './StudentsFilters'
import FeedFilters from './FeedFilters'
import DiaryFilters from './DiaryFilters'
import CohortsFilters from './CohortsFilters'
import VacanciesFilters from './VacanciesFilters'
import { PROJECT_START_DATE } from '../../../utils/constants'
import ContactsFilters from './ContactsFilters'
import MetricsFilters from './MetricsFilters'
import { useAppDispatch } from '../../../services/store/hooks'
import { setProfile } from '../../../services/slices/auth-slice'
import StudentsFiltersNew from './StudentsFiltersNew'

let lastCall
let lastCallTimer

function Filters({
  filterType,
  hook,
  filtersJSON,
  handleDownload,
  handleFeedExport,
  handleFunnelExport,
  handleAddCohort,
  handleAddVacancy,
  handleAddContact,
  contactsCount,
  isStatistics,
}) {
  const dispatch = useAppDispatch()
  const { filters, changeFilters } = hook
  const defaultValues = {
    start_date: format(PROJECT_START_DATE, 'yyyy-MM-dd'),
    end_date: format(Date.now(), 'yyyy-MM-dd'),
  }
  const [course, setCourse] = React.useState({ id: 0, name: 'Все курсы' })
  const [selectedCourses, setSelectedCourses] = React.useState([])
  const [cohort, setCohort] = React.useState({ id: 999, name: 'Все потоки' })
  const [cohortsCount, setCohortsCount] = React.useState([{ id: 999, name: 'Все потоки' }])
  const [type, setType] = React.useState({ id: 0, name: 'Все типы' })
  const [status, setStatus] = React.useState({ id: 0, name: 'Все статусы' })
  const [responsible, setResponsible] = React.useState({ id: 0, name: 'Любой ответственный' })
  const [startDate, setStartDate] = React.useState(PROJECT_START_DATE)
  const [endDate, setEndDate] = React.useState(Date.now())
  const [isCohortDisabled, setIsCohortDisabled] = React.useState(true)
  const [filtersList, setFilterList] = React.useState([])
  const [searchString, setSearchString] = React.useState('')
  const [student, setStudent] = React.useState(null)
  const searchState = [student, setStudent]

  function debounce(callee, timeoutMs) {
    return function perform(...args) {
      // В переменной previousCall мы будем хранить
      // временную метку предыдущего вызова
      const previousCall = lastCall
      lastCall = Date.now()
      if (previousCall && lastCall - previousCall <= timeoutMs) {
        clearTimeout(lastCallTimer)
      }
      lastCallTimer = setTimeout(() => callee(...args), timeoutMs)
    }
  }

  const {
    control,
    formState: { errors },
    setValue,
    clearErrors,
  } = useForm({ mode: 'onChange', resolver: yupResolver(filtersSchema), defaultValues })

  // useEffect для получения списка фильтров
  React.useEffect(() => {
    setFilterList({
      ...filtersJSON,
      courses: filtersJSON?.courses && [{ id: 0, name: 'Все курсы' }, ...filtersJSON?.courses],
      coursesOptions: filtersJSON?.courses,
      types: filtersJSON?.types && [
        { id: 0, name: 'Все типы' },
        ...filtersJSON?.types?.map((t) => ({ id: t?.value, name: t?.value })),
      ],
      statuses: filtersJSON?.statuses && [
        { id: 0, name: 'Все статусы' },
        ...filtersJSON?.statuses?.map((s) => ({ id: s?.value, name: s?.value })),
      ],
      staff: filtersJSON?.staff && [
        { id: 0, name: 'Любой ответственный' },
        ...filtersJSON?.staff?.map((s) => ({ id: s?.id, name: `${s?.first_name} ${s?.last_name}` })),
      ],
    })
  }, [filtersJSON])

  React.useEffect(() => {
    const newCourse = course.id || null
    const newCohort = cohort.id !== 999 ? cohort.id : null
    changeFilters({ course: newCourse, cohort_number: newCohort })
  }, [course, cohort])

  React.useEffect(() => {
    if (selectedCourses?.length > 0) changeFilters({ courses: selectedCourses })
    else changeFilters({ courses: undefined })
  }, [selectedCourses])

  React.useEffect(() => {
    let result
    if (student?.id) {
      result = { studentId: student?.id, id: undefined, name: undefined }
      dispatch(setProfile(student))
    } else {
      result = { studentId: student?.id }
    }
    changeFilters(result)
  }, [student])

  React.useEffect(() => {
    changeFilters({ type: type.id !== 0 ? type.id : null })
  }, [type])

  React.useEffect(() => {
    changeFilters({ status: status.id !== 0 ? status.id : null })
  }, [status])

  React.useEffect(() => {
    changeFilters({ staff: responsible.id !== 0 ? responsible.id : null })
  }, [responsible])

  // Перекрёстные юзЭффекты, чтобы обновлять состояния ошибок у обоих полей дат
  // + записать в объект хука данные дат
  React.useEffect(() => {
    setValue('start_date', format(startDate, 'yyyy-MM-dd'), {
      shouldValidate: true,
      shouldDirty: true,
    })
    // Если endDate не меньше, чем startDate, то заносим изменения в фильтр
    // Это наша валидация, она не даст сделать запрос на бэк при невалидном интервале
    // Так же не дёргаем бэк, если endDate из будущего
    if (differenceInDays(endDate, startDate) >= 0 && differenceInDays(Date.now(), endDate) >= 0) {
      changeFilters({ end_date: format(endDate, 'yyyy-MM-dd') })
    }
  }, [endDate])

  React.useEffect(() => {
    setValue('end_date', format(endDate, 'yyyy-MM-dd'), {
      shouldValidate: true,
      shouldDirty: true,
    })
    if (differenceInDays(endDate, startDate) >= 0 && differenceInDays(Date.now(), endDate) >= 0) {
      changeFilters({ start_date: format(startDate, 'yyyy-MM-dd') })
    }
  }, [startDate])

  const handleChangeMultipleCourses = (e) => setSelectedCourses(e.target.value)

  const handleChangeCourse = (event) => {
    setCourse({ id: event.target.value, name: event.target.name })
    // Ниже выставляю когорту в состояние "Все потоки", чтобы при переключении курса
    // Не было пустой строки в выпадающем списке
    setCohort({ id: 999, name: 'Все потоки' })
    if (event.target.value === 0) {
      setIsCohortDisabled(true)
      setCohort({ id: 999, name: 'Все потоки' })
    } else {
      setIsCohortDisabled(false)
      const { cohorts } = filtersList.courses.find((item) => item.id === event.target.value)
      const sortedCohorts = cohorts?.sort((a, b) => +a?.cohort_number - +b?.cohort_number)
      const expandedCohorts = sortedCohorts?.map((c) => ({ ...c, name: c?.cohort_number })) || []
      setCohortsCount([{ id: 999, name: 'Все потоки' }, ...expandedCohorts])
    }
  }
  const handleChangeType = (event) => {
    setType({ id: event.target.value, name: event.target.value })
  }
  const handleChangeStatus = (event) => {
    setStatus({ id: event.target.value, name: event.target.value })
  }
  const handleChangeResponsible = (event) => {
    setResponsible({ id: event.target.value, name: event.target.value })
  }
  const handleChangeCohort = (event) => {
    setCohort({ id: event.target.value, name: event.target.value })
  }

  const searchStudent = (string) => {
    // когда переходим в ленту по кнопке из списка студентов, нам нужно отображать
    // ленту конкретного студента + имя студента в поиске
    // если поиск изменили, то обнуляем айди и имя студента в фильтре, пользуемся поисковой строкой
    changeFilters({ search: string || null, id: null, name: null })
  }

  const debouncedHandleSearchChange = debounce(searchStudent, 1000)

  const handleSearchChange = (event) => {
    setSearchString(event.target.value)
    debouncedHandleSearchChange(event.target.value)
  }

  // Дата всегда будет в поиске. Поэтому выставляем её при монтировании компонента
  React.useEffect(() => {
    if (filters.id) {
      // setSearchString(filters.name)
      setStudent(filters.id)
    }
    changeFilters({ start_date: format(PROJECT_START_DATE, 'yyyy-MM-dd'), end_date: format(Date.now(), 'yyyy-MM-dd') })
  }, [])

  const handleDateIntervalStartChange = (val) => {
    clearErrors('start_date')
    if (val) {
      setValue('start_date', format(val, 'yyyy-MM-dd'), {
        shouldValidate: true,
        shouldDirty: true,
      })
      setStartDate(val)
    } else {
      setValue('start_date', null, { shouldValidate: true, shouldDirty: true })
      setStartDate(null)
    }
  }

  const handleDateIntervalEndChange = (val) => {
    if (val) {
      setValue('end_date', format(val, 'yyyy-MM-dd'), {
        shouldValidate: true,
        shouldDirty: true,
      })
      setEndDate(val)
    } else {
      setValue('end_date', null, { shouldValidate: true, shouldDirty: true })
      setEndDate(null)
    }
    clearErrors('end_date')
  }

  const dateProps = {
    startDate,
    endDate,
    handleDateIntervalStartChange,
    handleDateIntervalEndChange,
  }

  const courseProps = {
    courseId: course.id,
    courses: filtersList?.courses,
    handleChangeCourse,
  }

  const multipleCoursesProps = {
    coursesOptions: filtersList?.coursesOptions,
    selectedCourses,
    handleChangeMultipleCourses,
  }

  const cohortsProps = {
    cohortId: cohort.id,
    cohorts: cohortsCount,
    handleChangeCohort,
    isCohortDisabled,
  }

  const typesProps = {
    typeId: type.id,
    types: filtersList?.types,
    handleChangeType,
  }

  const staffProps = {
    staffId: responsible.id,
    staff: filtersList?.staff,
    handleChangeResponsible,
  }

  const statusProps = {
    statusId: status.id,
    statuses: filtersList?.statuses,
    handleChangeStatus,
  }

  switch (filterType) {
    case 'applications':
      return (
        <ApplicationFilters
          searchState={searchState}
          searchName={filters?.name}
          dateProps={dateProps}
          errors={errors}
          handleDownload={handleDownload}
          control={control}
          courseProps={courseProps}
          cohortsProps={cohortsProps}
          typesProps={typesProps}
          staffProps={staffProps}
          statusProps={statusProps}
        />
      )
    case 'students':
      return (
        <StudentsFilters
          searchState={searchState}
          searchName={filters?.name}
          dateProps={dateProps}
          errors={errors}
          handleDownload={handleDownload}
          control={control}
          courseProps={courseProps}
          cohortsProps={cohortsProps}
          statusProps={statusProps}
        />
      )
    case 'students2':
      return (
        <StudentsFiltersNew
          searchState={searchState}
          searchName={filters?.name}
          dateProps={dateProps}
          errors={errors}
          handleDownload={handleDownload}
          control={control}
          courseProps={courseProps}
          cohortsProps={cohortsProps}
          statusProps={statusProps}
          isStatistics={isStatistics}
        />
      )
    case 'feed':
      return (
        <FeedFilters
          searchState={searchState}
          searchName={filters?.name}
          dateProps={dateProps}
          errors={errors}
          handleFeedExport={handleFeedExport}
          handleFunnelExport={handleFunnelExport}
          control={control}
          courseProps={courseProps}
          cohortsProps={cohortsProps}
        />
      )
    case 'diary':
      return <DiaryFilters searchState={searchState} searchName={filters?.name} />
    case 'metrics':
      return <MetricsFilters searchState={searchState} searchName={filters?.name} />
    case 'cohorts':
      return (
        <CohortsFilters
          dateProps={dateProps}
          errors={errors}
          control={control}
          courseProps={courseProps}
          cohortsProps={cohortsProps}
          statusProps={statusProps}
          handleAddCohort={handleAddCohort}
          searchString={searchString}
          handleSearchChange={handleSearchChange}
        />
      )
    case 'vacancy':
      return (
        <VacanciesFilters
          searchString={searchString}
          handleSearchChange={handleSearchChange}
          dateProps={dateProps}
          errors={errors}
          control={control}
          multipleCoursesProps={multipleCoursesProps}
          statusProps={statusProps}
          handleAddVacancy={handleAddVacancy}
        />
      )
    case 'contacts':
      return (
        <ContactsFilters
          searchString={searchString}
          handleSearchChange={handleSearchChange}
          errors={errors}
          control={control}
          handleAddContact={handleAddContact}
          contactsCount={contactsCount}
        />
      )
    default:
      return null
  }
}

export default React.memo(Filters)
