/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-vars */
import React, { useState, useEffect, useMemo } from 'react'

import Button from '@material-ui/core/Button'

import IconButton from '@mui/material/IconButton'
import Dialog from '@mui/material/Dialog'
import DialogContent from '@mui/material/DialogContent'
import FormHelperText from '@mui/material/FormHelperText'

import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import SearchBar from '../../Searchers/SearchBar/SearchBar'
import CustomDatePickerInterval from '../../CustomDatePickerInterval/CustomDatePickerInterval'

import useStyles from './ExportDialog.style'

export default function ExportDialog({
  header,
  subheader,
  smallSize,
  hideSearch,
  openState,
  searchState,
  startDateState,
  endDateState,
  children,
  onSubmit,
  isFiltersError,
  filtersHelpersMessage,
  serverError,
}) {
  const [open, setOpen] = openState
  const classes = useStyles()
  const handleClose = () => setOpen(false)

  return (
    <Dialog className={`${classes.root} ${smallSize && classes.rootSmallSized}`} open={open} onClose={handleClose}>
      <IconButton onClick={handleClose} className={classes.closeButton}>
        <IconClose />
      </IconButton>

      <DialogContent className={classes.content}>
        <ul className={`${classes.headerContainer} ${smallSize && classes.headerContainerSmallSized}`}>
          <li style={{ alignSelf: 'start' }}>
            <h2 className={classes.header}>{header || 'Выгрузка'}</h2>
            <p className={classes.subheader}>{subheader || 'Выбери поля для выгрузки'}</p>
          </li>
          {!smallSize && (
            <>
              <li style={{ marginLeft: 38 }}>{!hideSearch && <SearchBar valueState={searchState} />}</li>
              <li style={{ width: 236, marginLeft: 21 }}>
                <CustomDatePickerInterval startDateState={startDateState} endDateState={endDateState} />
              </li>
            </>
          )}
        </ul>
        <div className={`${classes.mainContainer} ${smallSize && classes.mainContainerSmallSized}`}>{children}</div>
        <div className={classes.buttonContainer}>
          <div className={classes.error}>
            {isFiltersError && <FormHelperText>{filtersHelpersMessage}</FormHelperText>}
            {serverError && <FormHelperText>{serverError?.message || 'Произошла ошибка'}</FormHelperText>}
          </div>
          <Button className={classes.submitButton} onClick={onSubmit}>
            Выгрузить
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
