/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import { Box } from '@mui/material'
import useStyles from './Filters.style'
import SearchBar from '../Searchers/SearchBar/SearchBar'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'
import IconMetrics from '../../../assets/Icons/IconMetrics/IconMetrics'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { changeStatisticsMetricsState } from '../../../services/slices/statistics-metrics-slice'

function StudentsFiltersNew({
  searchState,
  searchName,
  dateProps,
  errors,
  handleDownload,
  control,
  courseProps,
  cohortsProps,
  statusProps,
  isStatistics = false,
}) {
  const classes = useStyles()
  const { isHidden } = useAppSelector((store) => store.statisticsMetrics)
  const dispatch = useAppDispatch()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { courseId, courses, handleChangeCourse } = courseProps
  const { cohortId, cohorts, handleChangeCohort, isCohortDisabled } = cohortsProps
  const { statusId, statuses, handleChangeStatus } = statusProps

  const changeMetricsStatus = () => {
    dispatch(changeStatisticsMetricsState(!isHidden))
    localStorage.setItem('metrics', JSON.stringify({ hideMetrics: !isHidden }))
  }

  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.filters__container}>
          <Box sx={{ display: 'flex', gap: '20px', alignItems: 'center' }}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
              Список студентов
            </Typography>
            <SearchBar valueState={searchState} nameValue={searchName} width={600} />
          </Box>
          <Button
            className={`${classes.button} ${classes.button_type_long} ${classes.button_color_white}`}
            onClick={changeMetricsStatus}
          >
            {isHidden ? (
              <Box sx={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <IconMetrics isHidden={isHidden} />
                <Typography>Показать метрики</Typography>
              </Box>
            ) : (
              <Box sx={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <IconMetrics isHidden={isHidden} />
                <Typography>Скрыть метрики</Typography>
              </Box>
            )}
          </Button>
        </div>
        <Box className={classes.filters__container}>
          <Box sx={{ display: 'flex', gap: '12px' }}>
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
            <TextfieldSelect
              name="course_filter"
              control={control}
              value={courseId}
              onChange={handleChangeCourse}
              items={courses}
              width={288}
            />
            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={188}
            />
            <TextfieldSelect
              name="cohorts_filter"
              control={control}
              value={cohortId}
              onChange={handleChangeCohort}
              items={cohorts}
              disabled={isCohortDisabled}
              width={124}
            />
          </Box>
          {!isStatistics && (
            <Button className={`${classes.button} ${classes.button_type_small}`} onClick={handleDownload}>
              Выгрузить данные
            </Button>
          )}
        </Box>
      </form>
    </div>
  )
}

export default React.memo(StudentsFiltersNew)
