/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import useStyles from './Filters.style'
import DateIntervalPicker from './date-interval-picker/date-interval-picker'
import TextfieldSelect from './textfield-select/textfield-select'
import TextFieldSearch from './textfield-search/textfield-search'

function CohortsFilters({
  dateProps,
  errors,
  control,
  courseProps,
  cohortsProps,
  statusProps,
  handleAddCohort,
  searchString,
  handleSearchChange,
}) {
  const classes = useStyles()
  const { startDate, endDate, handleDateIntervalStartChange, handleDateIntervalEndChange } = dateProps
  const { courseId, courses, handleChangeCourse } = courseProps
  const { cohortId, cohorts, handleChangeCohort, isCohortDisabled } = cohortsProps
  const { statusId, statuses, handleChangeStatus } = statusProps
  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
              Управление потоками
            </Typography>
            <TextFieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder="Поиск потока по номеру"
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
            />
            <DateIntervalPicker
              startDate={startDate}
              endDate={endDate}
              startError={!!errors.start_date?.message}
              endError={!!errors.end_date?.message}
              startHelperText={errors.start_date?.message}
              endHelperText={errors.end_date?.message}
              onStartChange={handleDateIntervalStartChange}
              onEndChange={handleDateIntervalEndChange}
            />
          </div>
        </div>
        <div className={classes.filters__container}>
          <div className={classes.filters}>
            <TextfieldSelect
              name="course_filter"
              control={control}
              value={courseId}
              onChange={handleChangeCourse}
              items={courses}
              width={288}
            />
            <TextfieldSelect
              name="cohorts_filter"
              control={control}
              value={cohortId}
              onChange={handleChangeCohort}
              items={cohorts}
              disabled={isCohortDisabled}
              width={140}
            />

            <TextfieldSelect
              name="status_filter"
              control={control}
              value={statusId}
              onChange={handleChangeStatus}
              items={statuses}
              width={140}
            />
          </div>

          <div className={classes.filters__buttonsContainer}>
            <Button className={`${classes.button} ${classes.button_type_long}`} onClick={handleAddCohort}>
              Добавить новый поток
            </Button>
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(CohortsFilters)
