import React from 'react'
import FormGroup from '@material-ui/core/FormGroup'
import useStyles from './CheckboxGroup.styles'

import CheckboxItem from './CheckboxItem/CheckboxItem'

const CheckboxGroup = ({ checkboxes, onCheckboxGroupChange, headItemTitle, isWide, ...props }) => {
  const classes = useStyles()
  const [parentCheckboxChecked, setParentCheckboxChecked] = React.useState(false)

  const handleParentCheckboxChange = (e, isChecked) => {
    const newCheckState = checkboxes.map((aCheckbox) => ({
      ...aCheckbox,
      checked: isChecked,
    }))
    onCheckboxGroupChange(newCheckState)
  }

  // eslint-disable-next-line no-unused-vars
  const updateParentWithChildren = () => {
    let allChecked = false
    for (let i = 0; i < checkboxes.length; i += 1) {
      if (checkboxes[i].checked) {
        allChecked = true
      } else {
        allChecked = false
        break
      }
    }
    setParentCheckboxChecked(allChecked)
  }

  React.useMemo(() => {
    updateParentWithChildren()
  }, [checkboxes])

  const handleChildCheckboxChange = (isChecked, index) => {
    const newCheckState = checkboxes.map((aCheckbox, i) =>
      index === i ? { ...aCheckbox, checked: isChecked } : aCheckbox,
    )
    onCheckboxGroupChange(newCheckState)
  }

  const renderCheckboxes = () => {
    if (!checkboxes) {
      return null
    }

    return checkboxes.map((aCheckbox, index) => (
      <CheckboxItem
        // eslint-disable-next-line react/no-array-index-key
        key={index}
        checkboxLabel={aCheckbox.label}
        value={aCheckbox.value}
        checked={aCheckbox.checked}
        onChange={(e, checkStatus) => handleChildCheckboxChange(checkStatus, index)}
      />
    ))
  }

  const groupClass =
    // eslint-disable-next-line no-nested-ternary
    checkboxes.length <= 4
      ? `${classes.checkboxGroupItemsLong} ${classes.checkboxGroupItems}`
      : isWide
      ? `${classes.checkboxGroupItemsWide} ${classes.checkboxGroupItems}`
      : classes.checkboxGroupItems

  return (
    <div className={classes.checkboxGroup} {...props}>
      <FormGroup>
        <div className={classes.formGroupContainer}>
          <div className={classes.checkboxGroupHeadItem}>
            <CheckboxItem
              checkboxLabel={headItemTitle}
              value="all"
              checked={parentCheckboxChecked}
              onChange={handleParentCheckboxChange}
            />
          </div>
          <div className={groupClass}>{renderCheckboxes()}</div>
        </div>
      </FormGroup>
    </div>
  )
}

export default CheckboxGroup
