/* eslint-disable no-unused-vars */
import React from 'react'
import { Controller } from 'react-hook-form'
import { TextField, MenuItem } from '@mui/material'
import IconExpandMore from '../../../../assets/Icons/IconExpandMore/IconExpandMore'
import useStyles from '../Filters.style'

const iconComponent = (props) => <IconExpandMore className={`${props.className}`} />

function TextFieldSelect({ name, control, value, onChange, items, disabled, textClass, width }) {
  const classes = useStyles()
  const selectMenuProps = {
    MenuProps: {
      classes: {
        paper: classes.paper,
        list: classes.list,
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        },
        transformOrigin: {
          vertical: 'top',
          horizontal: 'left',
        },
        getContentAnchorEl: null,
      },
    },
    IconComponent: iconComponent,
  }

  return (
    <Controller
      name={name}
      control={control}
      render={({ field }) => (
        <TextField
          {...field}
          id="course_filter"
          select
          value={value}
          onChange={onChange}
          variant="outlined"
          disabled={disabled}
          SelectProps={selectMenuProps}
          sx={{ width: '100%', minWidth: width, maxWidth: width }}
          className={`${classes.input} ${classes.inputSelect} ${textClass}`}
        >
          {items ? (
            items?.map((item) => (
              <MenuItem key={item.id} value={item.id} name={item.name} style={{ maxWidth: width - 5 }}>
                {item.name}
              </MenuItem>
            ))
          ) : (
            <MenuItem>Загрузка</MenuItem>
          )}
        </TextField>
      )}
    />
  )
}

export default React.memo(TextFieldSelect)
