import React from 'react'

type TFilterValue = string | number | boolean | Date | null

interface IFilters {
  [key: string]: TFilterValue
}

function useFilters(initialValues: IFilters = { page: 1, limit: 10 }) {
  const [filters, setFilters] = React.useState(initialValues)

  const changeFilters = (value: IFilters) => {
    setFilters({ limit: 10, ...filters, ...value, page: value?.ordering ? filters.page : value?.page || 1 })
  }

  return { filters, changeFilters }
}

export default useFilters
