import React, { useState } from 'react'
import { useForm } from 'react-hook-form'
import { format } from 'date-fns'
import { yupResolver } from '@hookform/resolvers/yup'
import { filtersSchema } from '../../../utils/schemas'
import DateIntervalPicker from '../Filters/date-interval-picker/date-interval-picker'
import { PROJECT_START_DATE } from '../../../utils/constants'

export default function CustomDatePickerInterval({ startDateState, endDateState }) {
  const [startDate, setStartDate] = startDateState || useState(PROJECT_START_DATE)
  const [endDate, setEndDate] = endDateState || React.useState(Date.now())
  const defaultValues = {
    start_date: format(PROJECT_START_DATE, 'yyyy-MM-dd'),
    end_date: format(Date.now(), 'yyyy-MM-dd'),
  }

  const {
    formState: { errors },
    setValue,
    clearErrors,
  } = useForm({ mode: 'onChange', resolver: yupResolver(filtersSchema), defaultValues })

  React.useEffect(() => {
    setStartDate(PROJECT_START_DATE)
  }, [])

  React.useEffect(() => {
    setValue('start_date', format(startDate, 'yyyy-MM-dd'), {
      shouldValidate: true,
      shouldDirty: true,
    })
  }, [endDate])

  React.useEffect(() => {
    setValue('end_date', format(endDate, 'yyyy-MM-dd'), {
      shouldValidate: true,
      shouldDirty: true,
    })
  }, [startDate])

  const handleDateIntervalStartChange = (val) => {
    if (val) {
      setValue('start_date', format(val, 'yyyy-MM-dd'), {
        shouldValidate: true,
        shouldDirty: true,
      })
      setStartDate(val)
    } else {
      setValue('start_date', null, { shouldValidate: true, shouldDirty: true })
      setStartDate(null)
    }
  }

  const handleDateIntervalEndChange = (val) => {
    if (val) {
      setValue('end_date', format(val, 'yyyy-MM-dd'), {
        shouldValidate: true,
        shouldDirty: true,
      })
      setEndDate(val)
    } else {
      setValue('end_date', null, { shouldValidate: true, shouldDirty: true })
      setEndDate(null)
    }
  }

  return (
    <DateIntervalPicker
      startDate={startDate}
      endDate={endDate}
      startError={!!errors.start_date?.message}
      endError={!!errors.end_date?.message}
      startHelperText={errors.start_date?.message}
      endHelperText={errors.end_date?.message}
      onStartChange={handleDateIntervalStartChange}
      onEndChange={handleDateIntervalEndChange}
    />
  )
}
