/* eslint-disable no-nested-ternary */
import React from 'react'
import { FormHelperText, InputAdornment } from '@material-ui/core'
import DateFnsUtils from '@date-io/date-fns'
import ruLocale from 'date-fns/locale/ru'
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers'
import useStyles from '../Filters.style'
import calendarIcon from '../../../../assets/calendar.svg'

function DateIntervalPicker({
  startDate,
  endDate,
  startError,
  endError,
  startHelperText,
  endHelperText,
  onStartChange,
  onEndChange,
}) {
  const classes = useStyles()
  return (
    <div className={classes.filters__datePickerContainer}>
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ruLocale}>
        <div className={classes.filters__datePickerStart}>
          <DatePicker
            InputProps={{
              startAdornment: (
                <InputAdornment position="end">
                  <img src={calendarIcon} alt="иконка календаря" className={classes.filters__datePickerButton} />
                </InputAdornment>
              ),
            }}
            cancelLabel="Отменить"
            className={classes.picker}
            inputVariant="outlined"
            format="dd MMM YYY"
            disableToolbar
            variant="dialog"
            value={startDate}
            error={startError}
            onChange={onStartChange}
          />
        </div>
      </MuiPickersUtilsProvider>
      {' — '}
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ruLocale}>
        <div className={classes.filters__datePickerEnd}>
          <DatePicker
            cancelLabel="Отменить"
            className={classes.picker}
            inputVariant="outlined"
            format="dd MMM yyyy"
            disableToolbar
            variant="dialog"
            value={endDate}
            error={endError}
            onChange={onEndChange}
          />
        </div>
      </MuiPickersUtilsProvider>
      <FormHelperText error className={classes.datePickerErrors}>
        {endHelperText || startHelperText || ' '}
      </FormHelperText>
    </div>
  )
}

export default React.memo(DateIntervalPicker)
