import React from 'react'
import { Controller } from 'react-hook-form'
import { TextField, InputAdornment } from '@material-ui/core'
import IconSearch from "../../../../assets/Icons/IconSearch/IconSearch"
import useStyles from '../Filters.style'

function TextFieldSearch({ value, control, onChange, placeholder, error, helperText }) {
  const classes = useStyles()
  return (
    <Controller
      name="search"
      control={control}
      render={({ field }) => (
        <TextField
          {...field}
          id="search_field"
          value={value}
          onChange={onChange}
          className={classes.search__input}
          variant="outlined"
          placeholder={placeholder}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <IconSearch />
              </InputAdornment>
            ),
          }}
          error={error}
          helperText={helperText}
        />
      )}
    />
  )
}

export default React.memo(TextFieldSearch)
