/* eslint-disable no-nested-ternary */
import React from 'react'
import { Typography, Button } from '@material-ui/core'
import useStyles from './Filters.style'
import TextfieldSearch from './textfield-search/textfield-search'

function ContactsFilters({ searchString, handleSearchChange, errors, control, handleAddContact, contactsCount }) {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <form className={classes.filters__form}>
        <div className={classes.search__container}>
          <div className={classes.searchInfo_container}>
            <Typography variant="h3" component="h2" style={{ fontWeight: 500 }}>
              Список контактов
            </Typography>
            <TextfieldSearch
              value={searchString}
              control={control}
              onChange={handleSearchChange}
              placeholder="Поиск по названию компании/контакта"
              error={!!errors.search_field?.message}
              helperText={errors.search_field?.message}
            />
          </div>
        </div>

        <div className={classes.filters__container} style={{ alignItems: 'center' }}>
          <Typography variant="body1" style={{ fontWeight: 500 }}>
            {`Всего контактов: ${contactsCount}`}
          </Typography>
          <div className={classes.filters__buttonsContainer}>
            <Button className={`${classes.button} ${classes.button_type_long}`} onClick={handleAddContact}>
              Добавить контакт
            </Button>
          </div>
        </div>
      </form>
    </div>
  )
}

export default React.memo(ContactsFilters)
